html,
body,
main,
[data-scroll] {
  overscroll-behavior: none;
}

* {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif,
    "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
}

canvas {
  display: block;
}

#c {
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: -1;
}

/* Project Styles */

#content {
  /* border: 3px solid red; */
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1;
}

.placeh {
  border: 2px solid white;
  margin: 10vw;
  width: 300px;
  height: 300px;
}
